import { NavItem } from "./AuthNav.styled";


const navItems = [
   { href: 'login', text: '' },
   { href: 'registration', text: '' }
];

export const AuthNav = () => {
   return (
      <nav>
         {navItems.map(({ href, text }) => (
            <NavItem key={href} to={href}>{text}</NavItem>
         ))}
      </nav>
   );
};